<template>
  <div id="SignupModal2">
    <div id="Signupbox2">
      <div id="signupContent2">
        <div class="signupContent2">
          <h2 id="signupTxt"></h2>
          <div class="signupProcess">
            <h4>1.   </h4>
            <p>
                    .  '
              '  .
            </p>
          </div>
          <div align="center" class="mt-2">
            <img
              id="kakaoImg"
              src="../assets/img/kakao_login_medium_wide.png"
              width="250px"
              @click="logInKakao"
            />
          </div>
          <div class="signupProcess">
            <h4>2.   </h4>
            <p>      .</p>
          </div>
          <b-form-group label="" label-for="userid">
            <b-form-input
              id="userid"
              ref="userid"
              v-model="user.userid"
              placeholder=" "
            >
            </b-form-input>
            <b-button
              variant="outline-danger"
              id="checkBtn"
              @click="overlapCheck"
              >  </b-button
            >
          </b-form-group>
          <b-form-group label="" label-for="userpwd">
            <b-form-input
              id="userpwd"
              ref="userpwd"
              v-model="user.userpwd"
              placeholder="  "
              type="password"
            ></b-form-input>
          </b-form-group>

          <b-form-group label=" " label-for="userpwdconfirm">
            <!-- @keypress.enter="signup" -->
            <b-form-input
              id="userpwdconfirm"
              ref="userpwdconfirm"
              v-model="user.userpwdconfirm"
              placeholder="  "
              type="password"
            ></b-form-input>
          </b-form-group>

          <b-form-group label="" label-for="username">
            <b-form-input
              id="username"
              ref="username"
              v-model="user.username"
              placeholder="  "
            ></b-form-input>
          </b-form-group>
          <b-form-group label="" label-for="gender">
            <b-form-input
              id="gender"
              ref="gender"
              v-model="user.gender"
              placeholder="  "
              readonly
            ></b-form-input>
          </b-form-group>
          <b-form-group label="" label-for="ages">
            <b-form-input
              id="ages"
              ref="ages"
              v-model="user.ages"
              placeholder="  "
              readonly
            ></b-form-input>
          </b-form-group>

          <b-form-group label="" label-for="email">
            <b-form-input
              id="email"
              ref="email"
              v-model="user.email"
              placeholder="  "
              readonly
            ></b-form-input>
          </b-form-group>

          <div id="introGroup">
            <div id="introduction">
              <b-form-group
                label=" "
                label-for="self-introduction"
              >
                <b-form-textarea
                  rows="6"
                  id="self-introduction"
                  ref="introduce"
                  v-model="user.introduce"
                  placeholder="  "
                ></b-form-textarea>
              </b-form-group>
            </div>

            <div id="profileLabel">
              <b-form-group label=" " label-for="profile">
                <input
                  type="file"
                  id="upload"
                  accept="image/*"
                  enctype="multipart/form-data"
                  @change="onFileSelected"
                />
              </b-form-group>
              <img
                @click="imgClicked"
                class="profileImg"
                v-if="!user.profileImg"
                src="../assets/img/default2.png"
                width="100%"
                height="100%"
              />
              <img
                @click="imgClicked"
                class="profileImg"
                v-if="user.profileImg"
                width="100%"
                height="100%"
                :src="user.profileImg"
              />
            </div>
          </div>
        </div>
        <div class="signupProcess">
          <h4>3.  </h4>
          <p>      </p>
        </div>
        <div>
          <b-button variant="danger" id="signupBtn" @click="signupCheck"
            > </b-button
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  name: "SignupModal",
  data: function () {
    return {
      user: {
        manager: 0,
        userid: "",
        username: "",
        userpwd: "",
        userpwdconfirm: "",
        ages: "",
        gender: "",
        email: "",
        profileImg: "",
        introduce: "",
        session_id: -1,
      },
      message: "",
      cnt: -1,
    };
  },
  created() {
    let code = this.$route.query.code;
    if (code != null || code != undefined) {
      axios
        .post(`https://i4a406.p.ssafy.io:5000/kakao/login/request`, code)
        .then((response) => {
          this.user.email = response.data["email"];
          this.user.ages = response.data["ages"];
          this.user.gender = response.data["gender"];
          this.user.username = response.data["nickname"];
          this.user.profileImg = response.data["profileImage"];
        });
    }
  },
  methods: {
    onFileSelected(event) {
      const file = event.target.files[0];
      this.user.profileImg = URL.createObjectURL(file);
    },
    imgClicked() {
      const uploadBtn = document.querySelector("#upload");
      uploadBtn.click();
    },
    overlapCheck: function () {
      axios
        .get(`https://i4a406.p.ssafy.io:5000/user/overlap/${this.user.userid}`)
        .then((response) => {
          this.cnt = response.data["cnt"];
          if (this.cnt == 0) {
            alert("   .");
          } else {
            alert(" .  !!");
          }
        });
    },
    signupCheck: function () {
      let err = true;
      let msg = "";
      !this.user.userid &&
        ((msg = " "),
        (err = false),
        this.$refs.userid.focus());
      err && this.cnt == -1 && ((msg = " !!"), (err = false));
      err &&
        this.cnt > 0 &&
        ((msg = " .  !!"), (err = false));
      err &&
        !this.user.userpwd &&
        ((msg = " "),
        (err = false),
        this.$refs.userpwd.focus());
      err &&
        !this.user.userpwdconfirm &&
        ((msg = "  "),
        (err = false),
        this.$refs.userpwdconfirm.focus());
      err &&
        !this.user.username &&
        ((msg = " "),
        (err = false),
        this.$refs.username.focus());
      err &&
        !this.user.introduce &&
        ((msg = " "),
        (err = false),
        this.$refs.introduce.focus());
      err &&
        !this.user.email &&
        ((msg = "    "),
        (err = false),
        this.$refs.email.focus());
      if (!err) {
        alert(msg);
      } else {
        this.pwdConfirmer();
        this.$bvModal.hide("modal-1");
      }
    },
    pwdConfirmer: function () {
      if (this.user.userpwd == this.user.userpwdconfirm) {
        this.$store
          .dispatch("SIGNUP", this.user)
          .catch(({ message }) => (this.msg = message));
        const fd = new FormData();
        if (document.getElementById("upload").files[0]) {
          fd.append("userid", this.user.userid);
          fd.append("upload", document.getElementById("upload").files[0]);
          axios
            .post(`https://i4a406.p.ssafy.io:5000/imgs/saveImage`, fd)
            .then();
        }
      } else alert("   .");
    },
    logInKakao() {
      window.Kakao.Auth.authorize({
        // url 
        redirectUri: `https://i4a406.p.ssafy.io:5000`,
      });
    },
  },
};
</script>

<style scoped>
h1 {
  font-family: yanoljaBold;
}
.profileImg {
  padding: 8%;
}
#Signup2 {
  display: relative;
}
#Signupbox {
  display: flex;
  justify-content: center;
  align-items: center;
}
#signupContent {
  height: 70%;
  width: 30%;
}
#profileLabel {
  width: 30%;
  height: 100%;
}
#upload {
  display: none;
}
#introGroup {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}
#introduction {
  width: 70%;
}
#signupTxt {
  text-align: center;
  margin-top: 60px;
}
#signupBtn {
  width: 100%;
}
#userid {
  width: 75%;
}
#checkBtn {
  width: 25%;
  height: 100%;
}

.form-control {
  display: inline;
}
#profileLabel img {
  border-radius: 0.25rem;
  border: 1px solid #ced4da;
  padding: 0;
  height: 177px;
}
#__BVID__19 {
  margin: 0;
}
#__BVID__21 {
  margin-bottom: 8px;
}
#profileLabel :nth-child(1) {
  margin: 0 0 8px 0;
}
.signupProcess {
  margin-top: 40px;
  margin-bottom: 20px;
}
#kakaoImg:hover {
  cursor: pointer;
}
</style>